.profile__subtitle {
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    margin: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    max-width: 336px;
}

@media screen and (max-width: 1023px) {
    .profile__subtitle {
        font-size: 16px;
        max-width: 280px;
    }
}

@media screen and (max-width: 767px) {
    .profile__subtitle {
        font-size: 14px;
        line-height: 17px;
    }
}